	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Finally","date":"26 Mar 2008","slug":"/2008-03-26-finally/","tags":"Y2008"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2362915679\" title=\"View 'CIMG0480' on Flickr.com\"><img src=\"http://farm3.static.flickr.com/2308/2362915679_a337a4f418.jpg\" alt=\"CIMG0480\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>We must be the last place in Germany to get any snow that stays on the ground, but for 90 minutes this morning Christopher could finally roll in the snow for the first time this <s>winter</s> spring. Then the snow was gone.</p>"},"next":{"fields":{"title":"Ship Ahoy","date":"24 Mar 2008","slug":"/2008-03-24-ship-ahoy/"}},"previous":{"fields":{"title":"Mixtape","date":"28 Mar 2008","slug":"/2008-03-28-mixtape/"}}},{"node":{"fields":{"title":"Ship Ahoy","date":"24 Mar 2008","slug":"/2008-03-24-ship-ahoy/","tags":"Y2008"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/2356857701\" title=\"View 'CIMG0478' on Flickr.com\"><img src=\"https://farm4.static.flickr.com/3202/2356857701_f40a9d1a7a.jpg\" alt=\"CIMG0478\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>The <a href=\"http://www.playmobil.de/on/demandware.store/Sites-DE-Site/de_DE/Product-Show?pid=4290&amp;CatalogCategoryID=Piraten\">Playmobil Camouflage Pirate Ship</a> is preparing for its maiden voyage in the bathtub!</p>"},"next":{"fields":{"title":"Twins Ad with Pat Neshek","date":"18 Mar 2008","slug":"/2008-03-18-twins-ad-with-pat-neshek/"}},"previous":{"fields":{"title":"Finally","date":"26 Mar 2008","slug":"/2008-03-26-finally/"}}},{"node":{"fields":{"title":"Twins Ad with Pat Neshek","date":"18 Mar 2008","slug":"/2008-03-18-twins-ad-with-pat-neshek/","tags":"Y2008"},"html":"<p>Twins pitcher and Minnesota native Pat Neshek has been keeping a <a href=\"http://patneshek.com/\">weblog</a> since he was in the minors. It's now spring training and Pat will be starting his second full year in the major leagues. The Twins now figure that Pat is well known enough to star in his own <a href=\"http://www.youtube.com/watch?v=Fv6pYVNxJQI\">TV ad</a> for the team. Well, maybe he's not as well known as they thought...</p>\n<p><object width=\"425\" height=\"355\"><param name=\"movie\" value=\"http://www.youtube.com/v/Fv6pYVNxJQI&hl=en\" /><param name=\"wmode\" value=\"transparent\" /><embed src=\"http://www.youtube.com/v/Fv6pYVNxJQI&hl=en\" type=\"application/x-shockwave-flash\" wmode=\"transparent\" width=\"425\" height=\"355\"></embed></object></p>\n<p>Pat himself <a href=\"http://eteamz.active.com/PatNeshek/#news4228830\">blogged about the spot</a> (but click fast, his hoster hasn't implemented permalinks), and if you're not from Minnesota you might need to <a href=\"http://www.google.com/search?q=joe+mauer+sideburns\">Google</a> to get the bit about the sideburns. All of the Twins TV ads are pretty good... the best one was last year with the <a href=\"http://www.youtube.com/watch?v=_7G3OtU6OkQ\">Johan Santana/Joe Nathan carpool</a>.</p>"},"next":{"fields":{"title":"Stroke of Insight","date":"15 Mar 2008","slug":"/2008-03-15-stroke-of-insight/"}},"previous":{"fields":{"title":"Ship Ahoy","date":"24 Mar 2008","slug":"/2008-03-24-ship-ahoy/"}}}],"pathPrefix":"","first":false,"last":false,"index":80,"pageCount":1101}}

